import React, { Component } from 'react'
import PropTypes from 'prop-types'
import QueryString from 'query-string'
import classnames from 'classnames'
import ScrollReveal from 'scrollreveal'
import FakeImg from 'react-fakeimg'
import Footer from '@/common/Footer'

import Loading from '@/Loading'
import ScrollIcon from '@/common/ScrollIcon'

class Landing extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      caroussel: 0
    }
  }

  componentWillMount() {
    document.title = 'PORTFOLIO 投資組合 | Stéphane Nguyen'
    // const url = 'https://api.instagram.com/v1/users/self/media/recent/?access_token=2980218616.5d6e3ce.77e73c5abfe645d690f9e85b3752ed4c'
    // const url = 'https://www.instagram.com/yoyodedongxi/media/'
  }

  componentDidMount() {
    this.setState({ loading: false })
    this.animation()
    setTimeout(() => {
      this.setState({ caroussel: 1 })
      this.startCaroussel()
    }, 1200)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  animation = () => {
    const sr = ScrollReveal()
    sr
      //.reveal('.initial', { delay: 1200 })
      .reveal('.socials', { origin: 'top', delay: 1200, scale: 1, })
      .reveal('.component .column.is-5', { origin: 'left', delay: 1200, scale: 1, })
      .reveal('.component .column.is-4', { origin: 'right', delay: 1200, scale: 1, })
      // .reveal('.developer', { origin: 'top', delay: 1200, scale: 1, })
  }

  startCaroussel = () => {
    const self = this
    this.interval = setInterval(() => {
      let { caroussel } = self.state
      caroussel = caroussel === 3 ? 1 : caroussel + 1
      self.setState({ caroussel })
    }, 5000);
  }

  mailTo = () => {
    const params = {
      subject: 'Hello from my website',
      body: 'Your message here'
    }
    document.location.href = `mailto:s.nguyen@outlook.com?${QueryString.stringify(params)}`
  }

  render() {

    const { loading, caroussel } = this.state

    return (
      <div className="landing">

        <section className="first hero is-fullheight">

          <div className="caroussel">
            <div className={classnames({
              show: caroussel === 1
            })} />
            <div className={classnames({
              show: caroussel === 2
            })} />
            <div className={classnames({
              show: caroussel === 3
            })} />
          </div>

          <div className="caroussel-bar is-hidden-mobile">
            <div className={classnames({
              active: caroussel === 1
            })} />
            <div className={classnames({
              active: caroussel === 2
            })} />
            <div className={classnames({
              active: caroussel === 3
            })} />
          </div>

          <div className="hero-body" />

          <p className="paragraph is-size-2 is-size-5-mobile is-uppercase">
            {'love fine design. enjoy working on artistic projects.'}
          </p>

          <div className="hero-foot">
            <div
              className="container is-pos-relative"
              style={{ height: '50px', marginBottom: '40px' }}
            >
              <ScrollIcon />
            </div>
          </div>

        </section>

        <section className="hero is-fullheight component">
          <div className="hero-body">
            <div className="columns">
              <div className="column is-5 is-offset-1 has-text-right">
                <FakeImg
                  bgColor="#e3008c"
                  fontColor="#ffffff"
                  text="colorful"
                  height={50}
                  width={150}
                  className="is-hidden-mobile"
                  style={{ margin: '10px' }}
                />
                <FakeImg
                  bgColor="#027b76"
                  fontColor="#ffffff"
                  height={100}
                  width={200}
                  className="is-hidden-mobile"
                  style={{ margin: '10px' }}
                />
                <FakeImg
                  text="it's a picture"
                  height={250}
                  width={500}
                  className="is-hidden-mobile"
                  style={{ margin: '0 10px' }}
                />
                <FakeImg
                  text="it's a picture"
                  height={200}
                  width={400}
                  className="is-hidden-tablet is-hidden-desktop"
                />
              </div>
              <div className="column is-4">
                <h3 className="title is-3 is-size-4-mobile is-uppercase">react-fakeimg</h3>
                <h4 className="subtitle">for mockup</h4>
                <p className="is-paragraph">
                  {'"react-fakeimg" will help you to generate fake image to make your mockup without any http requests using the power of canvas.'}
                  <br/><br/>
                  <a
                    href="https://huuduc.github.io/react-fakeimg/"
                    target="_blank"
                    className="is-uppercase is-size-5"
                  >
                    see more
                  </a>
                </p>
              </div>
            </div>
          </div>
        </section>

        <Footer router={this.context.router} />

        <Loading show={loading} />

      </div>
    )
  }

}

Landing.propTypes = {}
Landing.contextTypes = {
  router: PropTypes.object,
};

export default Landing



